// ---------------------------
// Template function suppliers
// ---------------------------

function computeTotal(items) {
  return items
    .map(item => item.quantity * item.product.inputPrice)
    .reduce((acc, curr) => acc + curr, 0)
}

function formatNumber(number) {
  const formatter = new Intl.NumberFormat('en-US', {
    minimumFractionDigits: 0
    // the default value for minimumFractionDigits depends on the currency
    // and is usually already 2
  })

  return formatter.format(number)
}

function itemLine({ product, quantity }) {
  const { supplierName, inputPrice } = product
  const totalPrice = quantity * inputPrice

  return `${quantity} ${supplierName} ${totalPrice}k\n`
}

function buildItemsBlock(items) {
  return items.map(itemLine).join('')
}

function sumLine(total) {
  return `Tổng: ${formatNumber(total)}k gửi đủ và gửi sớm nha chú\n`
}

/**
 *
 * Build response from extract item like:
 *
 *  100 me 450k
 *  5 muối đậu 22k
 *  5 trộn tắc 22k
 *  300 bánh tráng tắc 1350k
 *  90 bơ 720k
 *  26 sa tế tỏi 117k
 *  20 xấp dẻo cay 360k
 *  5 xấp dẻo tôm 110k
 *  3 xấp phơi sương 75k
 *  1 kg muối chay 90k
 *  3 hũ muối tôm 180k
 *  7 hũ chùm ruột 175k
 *  Tổng: 3,670k gửi đủ và gửi sớm nha chú
 *
 */
function buildResponse(items) {
  // Items example:
  // [
  //    {
  //       product: { name: 'Bơ', price: 10, supplierName: 'me', inputPrice: 4.5 },
  //       quantity: 10
  //    }
  // ]

  if (!items || items.length == 0) return ''

  // prettier-ignore
  return buildItemsBlock(items) 
       + sumLine(computeTotal(items))
}

module.exports = {
  itemLine,
  buildItemsBlock,
  sumLine,
  buildResponse
}
